import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const router = new VueRouter({
  mode: 'history',
  routes: [
    { path: '/',
      name: 'top',
      component: () => import('@/pages/Dashboard/index.vue'),
    },
    { path: '/auth/sign_in',
      name: 'signIn',
      component: () => import('@/pages/Auth/SignIn/index.vue'),
    },
    { path: '/auth/sign_up',
      name: 'signUp',
      component: () => import('@/pages/Auth/SignUp/index.vue'),
    },
    { path: '/auth/forget_password',
      name: 'forget',
      component: () => import('@/pages/Auth/PasswordForget/index.vue'),
    },
    { path: '/booking/detail',
      name: 'detail',
      component: () => import('@/pages/Booking/Detail/index.vue'),
    },
    { path: '/room/',
      name: 'room',
      component: () => import('@/pages/Room/index.vue'),
    },    

    { path: '/*', redirect: '/' },
  ]
})


router.beforeEach(async (to, from, next) => {
  if (to.matched.some(record => record.meta.requiresAuth)) {
    next()
  } else {
    next()
  }
})


export default router
